import { Route, Redirect } from "react-router-dom";
import React, { Suspense, lazy, Fragment } from "react";
import Loader from "react-loaders";

import { ToastContainer } from "react-toastify";
const Authentication = lazy(() => import("../../Panel/Authentication"));
const Invite = lazy(() => import("../../Panel/Invite"));
const Dashboards = lazy(() => import("../../Panel/Dashboards"));
const UserMaster = lazy(() => import("../../Panel/UserMaster"));
const Ticket = lazy(() => import("../../Panel/Ticket"));
const Skills = lazy(() => import("../../Panel/Skills"));
const SkillsType = lazy(() => import("../../Panel/SkillsType"));
const Profile = lazy(() => import("../../Panel/Profile"));
const Jobs = lazy(() => import("../../Panel/Jobs"));

const AppMain = () => {

    return (
        <Fragment>
            {/* UserMaster */}
            <Suspense fallback={
                <div className="loader-container">
                    <div className="loader-container-inner">
                        <div className="text-center">
                            <Loader type="line-scale"/>
                        </div>
                        <h6 className="mt-3">
                            Please wait while we load all the User Info
                        </h6>
                    </div>
                </div>
            }>
                <Route path="/user/master" component={UserMaster}/>
            </Suspense>
            {/* Ticket */}
            <Suspense fallback={
                <div className="loader-container">
                    <div className="loader-container-inner">
                        <div className="text-center">
                            <Loader type="line-scale"/>
                        </div>
                        <h6 className="mt-3">
                            Please wait while we load all the Ticket Info
                        </h6>
                    </div>
                </div>
            }>
                <Route path="/ticket" component={Ticket}/>
            </Suspense>
            {/* Jobs */}
            <Suspense fallback={
                <div className="loader-container">
                    <div className="loader-container-inner">
                        <div className="text-center">
                            <Loader type="line-scale"/>
                        </div>
                        <h6 className="mt-3">
                            Please wait while we load all the Jobs Info
                        </h6>
                    </div>
                </div>
            }>
                <Route path="/jobs" component={Jobs}/>
            </Suspense>
            {/* UserMaster */}
            <Suspense fallback={
                <div className="loader-container">
                    <div className="loader-container-inner">
                        <div className="text-center">
                            <Loader type="line-scale"/>
                        </div>
                        <h6 className="mt-3">
                            Please wait while we load all the Skills 
                        </h6>
                    </div>
                </div>
            }>
                <Route path="/skills" component={Skills}/>
            </Suspense>
               {/* UserMaster */}
            <Suspense fallback={
                <div className="loader-container">
                    <div className="loader-container-inner">
                        <div className="text-center">
                            <Loader type="line-scale"/>
                        </div>
                        <h6 className="mt-3">
                            Please wait while we load all the Skills Type 
                        </h6>
                    </div>
                </div>
            }>
                <Route path="/skills-type" component={SkillsType}/>
            </Suspense>
            
            
            {/* Profile */}
            <Suspense fallback={
                <div className="loader-container">
                    <div className="loader-container-inner">
                        <div className="text-center">
                            <Loader type="line-scale"/>
                        </div>
                        <h6 className="mt-3">
                            Please wait while we load Profile Page
                        </h6>
                    </div>
                </div>
            }>
                <Route path="/profile" component={Profile}/>
            </Suspense>
            

             {/* Pages */}

            <Suspense fallback={
                <div className="loader-container">
                    <div className="loader-container-inner">
                        <div className="text-center">
                            <Loader type="line-scale-party"/>
                        </div>
                        <h6 className="mt-3">
                            Please wait while we load all the Pages examples
                            <small>Because this is a demonstration we load at once all the Pages examples. This wouldn't happen in a real live app!</small>
                        </h6>
                    </div>
                </div>
            }>
                <Route path="/user" component={Authentication}/>
            </Suspense>

            {/* Dashboards */}

            <Suspense fallback={
                <div className="loader-container">
                    <div className="loader-container-inner">
                        <div className="text-center">
                            <Loader type="ball-grid-cy"/>
                        </div>
                        <h6 className="mt-3">
                            Please wait while we load all the Dashboards examples
                            <small>Because this is a demonstration, we load at once all the Dashboards examples. This wouldn't happen in a real live app!</small>
                        </h6>
                    </div>
                </div>
            }>
                <Route path="/dashboards" component={Dashboards}/>
            </Suspense>


            <Route exact path="/" render={() => (
                <Redirect to="/dashboards/index"/>
            )}/>
            <ToastContainer/>
        </Fragment>
    )
};

export default AppMain;
